import React, { Component } from 'react';
import {  MDBRow, MDBCol, MDBMedia } from "mdbreact";
import "./About.css";
class About extends Component {
    state = {  }
    render() { 
        return ( 
          <div className="about" id="about">
        
            <MDBMedia className="about-heading" >
                <h3 className="mt-4 about-heading ">About Me</h3>
            </MDBMedia><br/><br/>
            <MDBRow className="mb-4">
            
              <MDBCol md="3">
                <img src="images/portfolio.jpg" style={{width:"200px", height:"200px"}} className="rounded float-left" alt="myimage" />
                
              </MDBCol>
              <MDBCol md="8" className="about-text">
                <p>I live in downtown LA.<br/>
                  Full stack web developer.<br/> 
                  I have excellent interpersonal and communication abilities.
                  Enjoys being working as a team and succeeds in high pressure and challeng working environment.
                  hardworking and honest . I am a good timekeeper and always willing to learn new skills.
                  I am friendly, helpful and polite, have a good sense of humor. always up for new challenges. 
                </p>
              </MDBCol>
            </MDBRow>
            
          </div>
          
         );
    }
}
 
export default About;